.slider-box{
  padding: 0 0 214px 0;
  background-image: url("../images/prostok_t_4_1101.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.slider-title-txt{
  text-align: center;
  padding: 43px 0 0 0;
}
.quality-has-price{
  font-size: 14px;
  line-height: 48px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: #008ed6;
}
.pricings-plans{
  font-size: 36px;
  font-weight: 600;
  letter-spacing: 0.1px;
  color: #1a1a1a;
  line-height: 35px;
}
.fourth-double-underline{
  position: relative;
  &:before{
    content: "";
    border-bottom: 3px solid #008ed6;
    display: block;
    width: 20px;
    height: 2px;
    position: absolute;
    bottom: -29.5px;
    right: 104px;
  }
  &:after{
    content: "";
    border-bottom: 3px solid #008ed6;
    display: block;
    width: 20px;
    height: 2px;
    position: absolute;
    bottom: -29.5px;
    right: 125px;
  }
}
.slider-slides{
  margin: 120px 0 0 93px;
  display: flex;
  flex-direction: row;
}
.the-slider{
  max-width: 1440px;
  width: 100%;
  .slick-dots {
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    right: 46.52%;
    margin: 317px 0 0 0;
  }
  .slick-dots li button {
    position: relative;
    border-radius: 25px;
    border: none;
    color: transparent;
    margin: 3px 2px 0 2px;
    padding: 0;
    width: 12px;
    height: 12px;
    background-color: #b3b3b3;
  }
  .slick-dots li button {
    position: relative;
    border-radius: 25px;
    color: transparent;
    margin: 3px 2px 0 2px;
    padding: 0;
    width: 8px;
    height: 8px;
    background-color: rgba(255,255,255,0.0);
    border: 1px solid rgba(255,255,255,0.2);
  }
  .slider .slick-dots {
    justify-content: center;
    align-items: center;
    text-decoration: transparent;
  }
  .slick-dots{
    .slick-active{
      width: 14px;
      height: 14px;
      background-color: #ffffff;
      border-radius: 50%;
      margin: -1px 14px 0 0;
    }
    li {
      list-style: none;
      margin: 1px 16.6px 0 0;
    }
  }
  .slick-active {
    button {
      visibility: hidden;
    }
  }
  .slick-list {
    overflow: hidden;
    width: 1170px;
    margin: 0 0 0 22px;
  }
  .slick-track {
    margin: 0;
    display: flex;
    overflow: hidden;
    position: relative;
    justify-content: center;
  }
  .slick-slide {
    margin: 0 0 0 18px;
  }
  .slick-list {
    margin: 0 0 0 22px;
  }
}
